@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=SF+Pro:wght@300;400;500;600&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-montserrat: Montserrat;
  --font-sf-pro: "SF Pro";

  /* font sizes */
  --font-size-base: 16px;
  --font-size-5xl: 24px;
  --font-size-xl: 20px;
  --font-size-13xl: 32px;

  /* Colors */
  --color-white: #fff;
  --color-darkslategray: #484848;
  --color-black: #000;
  --color-lavender: #ebe4ff;
  --color-gainsboro: #e0e4eb;
  --color-gray-100: #8e8e8e;
  --color-gray-200: #462c2c;
  --color-slategray: #4f617d;
  --color-brown: #c54242;
  --color-yellowgreen: #72a701;
  --color-whitesmoke-100: #eee;
  --color-dimgray-100: #5a5a5a;

  /* border radiuses */
  --br-mini: 15px;
  --br-11xl: 30px;
  --br-3xs: 10px;
  --br-xl: 20px;
}
